.ans{
  background-color: rgb(38, 38, 105);
  text-align: right;
  color: white;
  font-size: larger;
}
.c{
  top:50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%,-50%);
  margin: 0 auto;
  border: 2px solid black;
  border-radius: 5px;
  background-color:black;
  
}
.keypad{

  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-auto-rows: minmax(60px,auto);
}
input[type="text"]{
  height:60px;
  width:250px;
  background-color:rgb(38, 38, 105);
  text-align: right;
  font-size: large;
  color: white;
}